@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700;800;900&display=swap);
*, *::after, *::before{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Montserrat', sans-serif;
    overflow-x: hidden;
    -ms-overflow-style: none;
    scrollbar-width: none;
}
::-webkit-scrollbar{
    display: none;
}
a{
    color: #000;
    color: initial;
    text-decoration: none;
}
.__te_onlineBanking__App{
    background-color: black;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}
.__te_onlineBanking__LandingPage{
    width: 100vw;
    min-height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}
.__te_onlineBanking__Button{
    color: rgb(0, 0, 0);
    min-width: 12vw;
    border: none;
    border-radius: 5px;
    padding: 1vw 2vw;
    background-color: rgb(255, 255, 255);
    outline: none;
    font-size: 1.1vw;
    font-weight: 700;
    margin: 1vw 2vw;
    cursor: pointer;
}
.__te_onlineBanking__Modal{
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-image: linear-gradient(-45deg, rgba(255, 255, 255, 0.11),rgba(0, 0, 0, 0.39));
    border: rgba(255, 255, 255, 0.466) solid 1px;
    -webkit-backdrop-filter: blur(5px);
            backdrop-filter: blur(5px);
    border-radius: 5px;
    z-index: 9;
}
.__te_onlineBanking__Modal .content{
    min-width: 30vw;
    min-height: 20vh;
    position: fixed;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    background-color: white;
    border-radius: 5px;
}
#closeBtn{
    position: absolute;
    top: 0;
    right: 0;
    margin: 1vw;
    /* width: 3vw; */
    /* margin: 0.3vw; */
    cursor: pointer;
    border: none;
    outline: none;
    /* padding: 0.2vw; */
    font-size: 1vw;
    font-weight: 700;
    /* text-transform: uppercase; */
    background-color: rgba(255, 255, 255, 0);
}
.__te_onlineBanking__Message{
